.landing-page {
  max-width: 450px;
  margin: 0 auto;
}

.landing-page .heading {
  margin-top: 30vh;
}

.landing-page .subheading {
  margin: 0;
}

.landing-page .options {
  margin-top: 20px;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
}
.landing-page .options .option {
  background-color: rgb(2, 2, 110);
  color: white;
  padding: 1em 5em;
  text-decoration: none;
  text-transform: uppercase;
  margin: 10px 15px;
  cursor: pointer;
